
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
html, body {
  height: 100%;
  width: 100%;
  overflow: hidden;
}
@media only screen and (min-width: 769px) {
html, body {
    margin: auto;
    width: 500px;
}
}
a {
  text-decoration: none;
}
i {
  cursor: pointer;
  display: inline-block;
  width: 30px;
  height: 30px;
}
li {
  list-style: none;
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: " ";
}
.clearfix:after {
  clear: both;
}
.clearfix {
  zoom: 1;
}
#app {
  position: relative;
  width: 100%;
  height: 100%;
}



